:root {
  --color-main: #f67174;
  --color-bg: #171931;
  --colorl-light-bg: #1f213e;
  --color-blue: #75f3f7;
  --color-purple: #d880f5;
  --color-text: #d8e0fe;
}

*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

body {
  background-color: var(--color-bg);
  color: white;
}

button {
  border: none;
  background: none;
  cursor: pointer;
  color: inherit;
}

ul {
  list-style-type: none;
}

[data-reach-dialog-content] {
  border-radius: 20px;
  color: var(--color-bg);
  padding: 2rem 2rem 2rem 2rem;
  position: relative;
  max-width: 448px;
}

[data-reach-slider-input] {
  flex-grow: 1;
}

/* Listbox */
/* https://github.com/reach/reach-ui/issues/736 */

[data-reach-listbox-input] {
  display: inline-block;
  position: relative;
}

[data-reach-listbox-popover] {
  z-index: 100;
}

@media (max-width: 768px) {
  [data-reach-dialog-content] {
    width: 90vw;
  }
}

